import {
  createStyles,
  Fab,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { PlayArrow, Pause } from "@material-ui/icons";
import { toInteger } from "lodash";
import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { useLocalStorageState } from "../../hooks/useLocalStorageState";
import { TimerConfiguration } from "../../types/timerConfiguration";
import Stopwtach from "../../util/stopwatch";

const audioUrl =
  "https://notificationsounds.com/storage/sounds/file-sounds-1235-swift-gesture.mp3";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: { display: "flex", flexDirection: "column", height: "100%" },
    play: {
      display: "flex",
      flex: 2,
      alignItems: "center",
      justifyContent: "center",
    },
    display: {
      display: "flex",
      flex: 2,
      alignItems: "center",
      justifyContent: "center",
    },
    title: {
      display: "flex",
      flex: 1,
      alignItems: "center",
      justifyContent: "center",
    },
  })
);
let stopwatch: Stopwtach | null = null;
const Play: React.FC<{}> = () => {
  const secRef = useRef<any>();
  const hunRef = useRef<any>();
  const classes = useStyles();
  const { index } = useParams();
  const timerListHook = useLocalStorageState("timerList", []);
  const list: TimerConfiguration[] = timerListHook[0];
  const timeConfig = list[toInteger(index)];

  const [currentTime, setCurrentTime] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);

  const [second, setSecond] = useState(0);
  const [hundredthSec, setHundredthSec] = useState(0);
  useEffect(() => {
    stopwatch = new Stopwtach(secRef.current, hunRef.current, () => {
      navigator.vibrate([100, 200, 300, 400]);
      var audio = new Audio(audioUrl);
      audio.play();

      setCurrentTime((x) => !x);
    });
  }, [setCurrentTime, timeConfig]);

  useEffect(() => {
    stopwatch!.reset(
      currentTime ? timeConfig.workOutTimer : timeConfig.restTimer
    );
  }, [currentTime, timeConfig]);

  useEffect(() => {
    if (isPlaying) stopwatch?.start();
    else stopwatch?.stop();
  }, [isPlaying]);

  useEffect(() => {
    setCurrentTime(true);
  }, [timeConfig]);

  return (
    <div className={classes.root}>
      <div className={classes.title}>
        <Typography paragraph>
          {timeConfig.workOutTimer}:{timeConfig.restTimer}
        </Typography>
      </div>
      <div className={classes.display}>
        <Typography variant="h3" innerRef={secRef}>
          00
        </Typography>
        <Typography paragraph innerRef={hunRef}>
          00
        </Typography>
      </div>
      <div className={classes.play}>
        <Fab
          color="primary"
          aria-label="add"
          size="large"
          onClick={() => setIsPlaying(!isPlaying)}
        >
          {isPlaying ? <Pause /> : <PlayArrow />}
        </Fab>
      </div>
    </div>
  );
};

export default Play;
