import { padStart } from "lodash"

export default class Stopwtach {
    interval: any
    currentSecond = 0
    currentHundredth = 0
    limit: number = 0
    constructor(
        public elementSecond: HTMLElement | null,
        public elementHundredth: HTMLElement | null,
        public finishCallback: () => void | null) { }

    public start() {
        this.interval = setInterval(() => {

            this.currentHundredth += 1
            if (this.currentHundredth >= 100) {
                this.currentSecond += 1
                this.currentHundredth = 0
            }

            if (this.currentSecond > this.limit) {
                this.finishCallback()
            }

            this.elementSecond!.innerHTML = this.currentSecond + ""
            this.elementHundredth!.innerHTML = padStart(this.currentHundredth + "", 2, "0")
        }, 10)
    }

    public stop() {
        clearInterval(this.interval)
    }

    public reset(seconds: number) {
        this.limit = seconds
        this.currentSecond = 0
        this.currentHundredth = 0
    }
}

